        implementacion9: 'Una vez modificadas las propiedades del formulario y de sus controles queda configurado de la siguiente manera, como se puede ver en la Figura 1.2.5.',
        imagen10: './assets/images/Capitulos/I/media/image23.png',
        figuraNum10: 'Figura 1.2.5. ',
        figura10: 'El Formulario y sus controles modificados sus propiedades. ',
        implementacion10: 'Esta aplicacin aceptar el ingreso de un valor flotante o entero en una Caja de Texto (TextBox) y desplegar los resultados en otras dos Cajas de Texto (TextBoxes) y el grfico de la figura se desplegar en una Caja de Imagen (PictureBox).',
        implementacion11: 'La clase CSquare tiene los siguientes datos miembro: a) mSide, que representa al lado del cuadrado que es de tipo float; b) mPerimeter, mArea, que representan al permetro y al rea del cuadrado que son tambin de tipo float; c) un objeto mGraph de tipo Graphics; d) una pluma de dibujo llamada mPen de tipo Pen; e) una constante de tipo float llamada SF definida como un factor de escalamiento (scale factor).',
        implementacion12: 'Esta clase tambin posee un grupo de mtodos de acuerdo a las acciones que puede hacer y que bsicamente sern funciones para poder representar y operar un cuadrado en el plano, entre las ms importantes estn las siguientes:',
        metodos: [{t:'a)	Constructor sin argumentos',a:'Este mtodo inicializa los primeros tres datos miembros de la clase CSquare con el valor de cero.',
          b:
            `
        // Constructor sin parmetros.
        public CSquare()
        {
            mSide = 0.0f; mPerimeter = 0.0f; mArea = 0.0f;
        }
        `
        },
          {t:'b)	Funcin miembro ReadData. ',a:'Esta funcin permite leer el valor del lado del cuadrado ingresado en una caja de texto. En este caso es necesario realizar un casting o conversin de tipo de datos de String a float utilizando la funcin float.Parse() y adems se utiliza la funcin try-catch para el manejo y control de excepciones, en caso de que el usuario en lugar de ingresar nmeros ingrese letras por error.',
            b:
              `
        // Funcin que lee el lado del cuadrado.
        public void ReadData(TextBox txtSide)
        {
            try
            {
                mSide = float.Parse(txtSide.Text);
            }
            catch
            {
                MessageBox.Show("Ingreso no vlido...", "Mensaje de error");
            }
         }
        `
          },
          {t:'c)	Funcin miembro Perimeter. ',a:'Esta funcin permite calcular el permetro de un cuadrado, para lo cual se utiliza una frmula matemtica para poder realizar de esta manera los respectivos clculos.',
            b:`
          // Funcin que calcula el permetro del cuadrado.
          public void PerimeterSquare()
          {
              mPerimeter = 4 * mSide;
          }
          `
          },
          {t:'d)	Funcin miembro Area. ',a:'Esta funcin permite calcular el rea de un cuadrado, para lo cual se utiliza una frmula matemtica para poder realizar de esta manera los respectivos clculos.',
            b:
              `
          // Funcin que calcula el rea del cuadrado.
          public void AreaSquare()
          {
              mArea = (float)Math.Pow(mSide, 2);
          }
          `
          },
          {t:'e)	Funcin miembro PrintData. ',a:'Esta funcin permite imprimir en cajas de texto los valores del permetro y del rea del cuadrado calculados, para lo cual es necesario realizar un casting o conversin de tipo float a String, utilizando la funcin ToString().',
            b:
              `
          // Funcin que imprime el permetro y el rea del cuadrado.
          public void PrintData(TextBox txtPerimeter, TextBox txtArea)
          {
              txtPerimeter.Text = mPerimeter.ToString();
              txtArea.Text = mArea.ToString();
          }
          `
          },
          {t:'f)	Funcin miembro InitializeData. ',a:'Esta funcin permite inicializar los datos miembro y los controles del formulario con valores por defecto, como en el caso de las cajas de texto se inicializan con el valor de [BLANK] utilizando los smbolos de comillas dobles (). Para inicializar la caja de imagen (picCanvas) se utiliza la funcin Refresh que significa refrescar o borrar.',
            b:
              `
          // Funcin que inicializa los datos y controles del cuadrado.
          public void InitializeData(TextBox txtSide, TextBox txtPerimeter,
                                     TextBox txtArea, PictureBox picCanvas)
          {
              mSide = 0.0f; mPerimeter = 0.0f; mArea = 0.0f;

              txtSide.Text = ""; txtPerimeter.Text = ""; txtArea.Text = "";
              // Mantiene el cursor titilando en una caja de texto.
              txtSide.Focus();
              picCanvas.Refresh();
          }
          `
          },
          {t:'g)	Funcin miembro PlotShape. ',a:'Esta funcin permite graficar un crculo, para lo cual en el objeto mGraph se asigna la funcionalidad de crear grficos de la caja de imagen (picCanvas). Luego se crea una pluma de color azul con una punta de ancho 3 conocida como Brush. Finalmente, se dibuja un cuadrado en las coordenadas (0,0) y con los valores del lado asignados al ancho y al largo del rectngulo multiplicados por un factor de escalamiento, para que la figura no sea muy pequea.',
            b:
              `
          // Funcin que grafica un cuadrado.
          public void PlotShape(PictureBox picCanvas)
          {
              mGraph = picCanvas.CreateGraphics();
              mPen = new Pen(Color.Blue, 3);
              // Graficar un cuadrado en funcin de un rectngulo.
              mGraph.DrawRectangle(mPen, 0, 0, mSide * SF, mSide * SF);
          }
          `
          },
          {t:'h)	Funcin miembro CloseForm. ',a:'Esta funcin permite cerrar un formulario utilizando la funcin Close(), que es llamada por el objeto ObjForm el cual es un parmetro de la funcin que recibe un objeto de tipo Form.',
            b:
              `
          // Funcin que cierra un formulario.
          public void CloseForm(Form ObjForm)
          {
            ObjForm.Close();
          }
          `}
        ],
