        implementacion13: 'La implementacin de la clase CRectangle, se presenta a continuacin en la Tabla 1.1.2, que contiene datos miembro y funciones miembro que realizan diferentes operaciones.',
        tabla2:'Tabla 1.1.2. ',
        tabladetalle2:'Cdigo de la clase CRectangle del programa.',
        tablaDatos2:
        `
        using System;
        using System.Collections.Generic;
        using System.Linq;
        using System.Text;
        using System.Threading.Tasks;
        using System.Drawing;
        using System.Windows.Forms;

        namespace WinAppRectangle
        {
            class CRectangle
            {
                // Datos Miembro (Atributos).

                // Ancho del rectngulo.
                private float mWidth;
                // Largo del rectngulo.
                private float mHeight;
                // Permetro del rectngulo.
                private float mPerimeter;
                // rea del rectngulo.
                private float mArea;
                // Objeto que activa el modo grfico.
                private Graphics mGraph;
                // Constante scale factor (Zoom In/Zoom Out).
                private const float SF = 20;
                // Objeto bolgrafo que dibuja o escribe en un lienzo (canvas).
                private Pen mPen;

                // Funciones Miembro (Mtodos).

                // Constructor sin parmetros.
                public CRectangle()
                {
                    mWidth = 0.0f; mHeight = 0.0f;
                    mPerimeter = 0.0f; mArea = 0.0f;
                }

                // Funcin que lee los datos de entrada del rectngulo.
                public void ReadData(TextBox txtWidth, TextBox txtHeight)
                {
                    try
                    {
                        mWidth = float.Parse(txtWidth.Text);
                        mHeight = float.Parse(txtHeight.Text);
                    }
                    catch
                    {
                        MessageBox.Show("Ingreso no vlido...",
                                        "Mensaje de error");
                    }
                }

                // Funcin que calcula el permetro del rectngulo.
                public void PerimeterRectangle()
                {
                    mPerimeter = 2 * mWidth + 2 * mHeight;
                }

                // Funcin que calcula el rea del rectngulo.
                public void AreaRectangle()
                {
                    mArea = mWidth * mHeight;
                }

                // Funcin que imprime el permetro y el rea del rectngulo.
                public void PrintData(TextBox txtPerimeter, TextBox txtArea)
                {
                    txtPerimeter.Text = mPerimeter.ToString();
                    txtArea.Text = mArea.ToString();
                }

                // Funcin que inicializa los datos y controles del rectngulo.
                public void InitializeData(TextBox txtWidth, TextBox txtHeight,
                                            TextBox txtPerimeter, TextBox txtArea,
                                            PictureBox picCanvas)
                {
                    mWidth = 0.0f; mHeight = 0.0f;
                    mPerimeter = 0.0f; mArea = 0.0f;

                    txtWidth.Text = ""; txtHeight.Text = "";
                    txtPerimeter.Text = ""; txtArea.Text = "";
                    // Mantiene el cursor titilando en una caja de texto.
                    txtWidth.Focus();
                    picCanvas.Refresh();
                }

                // Funcin que grafica un rectngulo.
                public void PlotShape(PictureBox picCanvas)
                {
                    mGraph = picCanvas.CreateGraphics();
                    mPen = new Pen(Color.Blue, 3);
                    // Graficar un rectngulo.
                    mGraph.DrawRectangle(mPen, 0, 0, mWidth * SF, mHeight * SF);
                }

                // Funcin que cierra un formulario.
                public void CloseForm(Form ObjForm)
                {
                    ObjForm.Close();
                }
            }
        `,
        implementacion14: 'La implementacin de la clase frmRectangle, se presenta a continuacin en la Tabla 1.1.3, que es la clase que representa a la interfaz grfica de usuario de la figura geomtrica conocida como Rectngulo.',
        tabla3:'Tabla 1.1.3. ',
        tabladetalle3:'Cdigo de la clase frmRectangle del programa.',
        tablaDatos3:
        `
        public partial class frmRectangle : Form
        {
            // Definicin de un objeto de tipo CRectangle.
            private CRectangle ObjRectangle = new CRectangle();

            public frmRectangle()
            {
                InitializeComponent();
            }

            private void frmRectangle_Load(object sender, EventArgs e)
            {
                // Inicializacin de los datos y controles.
                // Llamada a la funcin InitializeData.
                ObjRectangle.InitializeData(txtWidth, txtHeight,
                                            txtPerimeter, txtArea,
                                            picCanvas);
            }

            private void btnCalculate_Click(object sender, EventArgs e)
            {
                // Lectura de datos.
                // Llamada a la funcin ReadData.
                ObjRectangle.ReadData(txtWidth, txtHeight);
                // Clculo del permetro de un rectngulo.
                // Llamada a la funcin PerimeterRectangle.
                ObjRectangle.PerimeterRectangle();
                // Clculo del rea de un rectngulo.
                // Llamada a la funcin AreaRectangle.
                ObjRectangle.AreaRectangle();
                // Impresin de datos.
                // Llamada a la funcin PrintData.
                ObjRectangle.PrintData(txtPerimeter, txtArea);
                // Graficacin de un rectngulo.
                // Llamada a la funcin PlotShape.
                ObjRectangle.PlotShape(picCanvas);
            }

            private void btnReset_Click(object sender, EventArgs e)
            {
                // Inicializacin de los datos y controles.
                // Llamada a la funcin InitializeData.
                ObjRectangle.InitializeData(txtWidth, txtHeight,
                                            txtPerimeter, txtArea,
                                            picCanvas);
            }

            private void btnExit_Click(object sender, EventArgs e)
            {
                // Cierre de un formulario.
                // Llamada a la funcin CloseForm.
                ObjRectangle.CloseForm(this);
            }
        }
        `,
        implementacion15: 'La clase frmRectangle tiene como datos miembro un objeto de tipo CRectangle llamado ObjRectangle, el cual es el encargado de acceder a las funciones pblicas de la clase CRectangle para realizar diferentes clculos y operaciones. ',
        implementacion16: 'En el evento Load del formulario, cuyo procedimiento se llama frmRectangle_Load(), se utiliza el objeto ObjRectangle, para llamar a la funcin pblica InitializeData(), que se encarga de inicializar los datos y controles del formulario. En el evento Click del botn btnCalculate, cuyo procedimiento se llama btnCalculate_Click(), se utiliza el objeto ObjRectangle, para llamar a las funciones pblicas: ReadData(), PerimeterRectangle(), AreaRectangle(), PrintData() y PlotShape(). En el evento Click del botn btnReset, cuyo procedimiento se llama btnReset_Click(), se utiliza el objeto ObjRectangle, para llamar a la funcin pblica InitializeData(). En el evento Click del botn btnClose, cuyo procedimiento se llama btnClose_Click(), se utiliza el objeto ObjRectangle, para llamar a la funcin pblica CloseForm(), que permite cerrar el formulario.',
