        diagramaC1: 'El diagrama de clases de esta aplicacin se muestra en la Figura 1.2.3 con notacin UML. En este diagrama se puede ver que la aplicacin est compuesta por seis clases, donde la clase Program se compone por la clase frmSquare que resulta ser una clase derivada de la clase Form. La clase frmSquare se compone de un objeto de tipo CSquare. ',
        diagramaC2: 'La clase CSquare se compone de un objeto de tipo Graphics llamado mGraph, que maneja el modo grfico de la aplicacin y de un objeto de tipo Pen llamado mPen, para dibujar sobre un lienzo representado por un pictureBox cuyo identificador es picCanvas. Adems, se tiene una constante llamada SF (scale factor) de tipo float que permite realizar un acercamiento (Zoom-In) y un alejamiento (Zoom-Out) sobre la figura geomtrica. Los otros atributos que se tienen en la clase CSquare son el lado, el permetro y el rea representados por los datos miembros mSide, mPerimeter y mArea que son de tipo float.',
