        implementacion15: 'La clase frmCircle tiene como datos miembro un objeto de tipo CCircle llamado ObjCircle, el cual es el encargado de acceder a las funciones pblicas de la clase CCircle para realizar diferentes clculos y operaciones. ',
        implementacion16: 'En el evento Load del formulario, cuyo procedimiento se llama frmCircle_Load(), se utiliza el objeto ObjCircle, para llamar a la funcin pblica InitializeData(), que se encarga de inicializar los datos y controles del formulario. En el evento Click del botn btnCalculate, cuyo procedimiento se llama btnCalculate_Click(), se utiliza el objeto ObjCircle, para llamar a las funciones pblicas: ReadData(), PerimeterCircle(), AreaCircle(), PrintData() y PlotShape(). En el evento Click del botn btnReset, cuyo procedimiento se llama btnReset_Click(), se utiliza el objeto ObjCircle, para llamar a la funcin pblica InitializeData(). En el evento Click del botn btnClose, cuyo procedimiento se llama btnClose_Click(), se utiliza el objeto ObjCircle, para llamar a la funcin pblica CloseForm(), que permite cerrar el formulario.',
        implementacion17: '',
