        implementacion18: 'Para correr la aplicacin basta con presionar la tecla F5 o el botn Start de ejecucin de la barra de mens de Visual Studio .NET. La Figura 1.3.6 muestra un ejemplo de la corrida de este programa, pero para saber que los resultados del problema son correctos, se debe examinar los resultados del programa cuidadosamente para asegurarnos que tengan sentido. El ejemplo mostrado de la salida del programa proporciona una buena prueba de la solucin porque es relativamente fcil calcular el rea y el permetro manualmente o utilizando una calculadora. Para un valor de radio igual a 4 se puede observar la grfica de la figura geomtrica en la caja de imagen (picCanvas). El permetro deber ser dos veces el radio multiplicado por el valor de PI, que tambin es un valor fcil de calcular manualmente o utilizando una calculadora. El rea deber ser igual al producto de PI por el valor del radio elevado al cuadrado, que tambin es un valor fcil de calcular manualmente o utilizando una calculadora. Para verificar que el programa funciona adecuadamente, se deber ingresar unos pocos valores de prueba correspondientes a los radios de algunos crculos. No se necesita probar ms que con unos pocos casos de prueba para verificar que este programa est correcto. ',
