        figura1: 'El Tringulo y sus frmulas.',
        analisis: 'Claramente, se puede ver que las entradas del problema son los tres lados del tringulo. Hay tres salidas requeridas: el permetro, el rea del tringulo y el grfico de la figura geomtrica. Partiendo de un conocimiento bsico de geometra, se sabe que hay una relacin entre los tres lados del tringulo con el permetro, con el semi-permetro y con el rea de la figura geomtrica. Las frmulas requeridas se incluyen en el requerimiento de los datos. En este programa referente al tringulo se considera la validacin de la existencia de un tringulo cuyo Teorema expresa que un tringulo cualquiera existe si y slo si la suma de cualquiera de sus dos lados es mayor que el tercer lado: (a+b>c)(a+c>b)(b+c>a).',
