        implementacion13: 'La implementacin de la clase CLine, se presenta a continuacin en la Tabla 1.5.2, que contiene datos miembro y funciones miembro que realizan diferentes operaciones.',
        tabla2:'Tabla 1.5.2. ',
        tabladetalle2:'Cdigo de la clase CLine del programa.',
        tablaDatos2:
        `using System;
        using System.Collections.Generic;
        using System.Linq;
        using System.Text;
        using System.Threading.Tasks;
        using System.Drawing;
        using System.Windows.Forms;

        namespace WinAppLines
        {
            class CLine
            {
                // Datos Miembro (Atributos).

                // Objeto que activa el modo grfico.
                private Graphics mGraph;
                // Objeto Punto que representa al punto inicial de
                // graficacin de una lnea.
                private Point mStartPoint;
                // Objeto Punto que representa al punto final de
                // graficacin de una lnea.
                private Point mEndPoint;
                // Objeto bolgrafo que dibuja o escribe una lnea.
                Pen mLinePen;
                // Objeto bolgrafo que dibuja o escribe una elipse.
                Pen mEllipsePen;

                // Funciones Miembro (Mtodos).

                // Constructor sin parmetros.
                public CLine()
                {
                    mStartPoint = new Point(0, 0);
                    mEndPoint = new Point(0, 0);
                }

                // Funcin que inicializa los datos y controles de la clase.
                public void InitializeData(PictureBox picCanvas, CheckBox chkPoint)
                {
                    mStartPoint.X = 0; mStartPoint.Y = 0;
                    mEndPoint.X = 0; mEndPoint.Y = 0;

                    picCanvas.Refresh();
                    chkPoint.Checked = false;
                }

                // Funcin que permite dibujar una lnea con el clic izquierdo
                // del ratn.
                public void DrawLineMouse(MouseEventArgs e, PictureBox picCanvas,
                                          CheckBox chkPoint)
                {
                    // Si se ha seleccionado el botn izquierdo del ratn, entonces
                    if (e.Button == MouseButtons.Left)
                    {
                        // Asignar la funcionalidad de crear grficos al objeto mGraph.
                        mGraph = picCanvas.CreateGraphics();

                        // Crear dos bolgrafos
                        mLinePen = new Pen(Color.Green, 1);
                        mEllipsePen = new Pen(Color.Red, 1);

                        // Asignar el punto final al punto inicial.
                        mStartPoint = mEndPoint;
                        // Asignar al punto final la coordenada del ratn.
                        mEndPoint = new Point(e.X, e.Y);

                        // Dibujar una lnea desde el punto actual hasta
                        // el nuevo punto.
                        mGraph.DrawLine(mLinePen, mStartPoint, mEndPoint);

                        // Si la casilla de verificacin del check box est
                        // marcada, dibujar un rectngulo que representa un punto.
                        if (chkPoint.Checked)
                        {
                            mGraph.DrawRectangle(mEllipsePen, e.X - 2, e.Y - 2, 4, 4);
                        }
                        // Dibujar un crculo para representar un punto.
                        else
                        {
                            mGraph.DrawEllipse(mEllipsePen, e.X - 2, e.Y - 2, 4, 4);
                        }
                        // Desechar los objetos
                        mLinePen.Dispose();
                        mEllipsePen.Dispose();
                        mGraph.Dispose();
                    }
                }
                // Funcin que cierra un formulario.
                public void CloseForm(Form ObjForm)
                {
                    ObjForm.Close();
                }
            }
        }
        `,
